	export default {
		name: 'UniGoodsNav',
		data() {
			return {
				selctIndex:null,
				isOpen:false,
				inputVal:``,
				isShoucang:false,
				isComment:false,//是否展示评论的专属图标
				isShow:true,
			}
		},
		props: {
			options: {
				type: Array,
				default () {
					return [{
						icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/dianpu.png',
						text: '店铺'
					}, {
						icon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/uni-ui/goodsnav/carts.png',
						text: '购物车'
					}]
				}
			},
			buttonGroup: {
				type: Array,
				default () {
					return [{
							text: '加入购物车',
							backgroundColor: '#ff0000',
							color: '#fff'
						},
						{
							text: '立即购买',
							backgroundColor: '#ffa200',
							color: '#fff'
						}
					]
				}
			},
			fill: {
				type: Boolean,
				default: false
			},

		},
		methods: {
			onClick(index, item) {
				console.log(123)
				if(index!=this.options.length-1){
					index!=0&&index!=1?this.selctIndex = index:"";
				}else{
					this.isShoucang = !this.isShoucang;
				};
				this.$emit('click', {
					index,
					content: item
				})
			},
			buttonClick(index, item) {
				if (uni.report) {
					uni.report(item.text, item.text)
				}
				getCurrentPages().$emit('buttonClick', {
					index,
					content: item
				})
			},
			//输入框的事件
			inputClick(){
				console.log(this)
			   this.isShow=false; 
			},
			//输入边框返回事件
			inputBack(){
				this.isShow=true;
				this.isOpen = false;
				this.$emit('biaoqingClick', {
					isOpen: this.isOpen
				})
			},
			confirm(e){
				console.log("点击确定",e);
                let user = this.commonData.getUser("../../pages/index/login");
				if(user){
					//模拟成功
					uni.showLoading({
					    title: '正在发送中....'
					});
					setTimeout(()=>{
					    uni.hideLoading();
						let data={
								headImgSrc: user.avatarUrl,
								userName: user.nickName,
								userLevel: '5',
								cenId: '19',
								sendTime: new Date().toLocaleDateString(),
								sendMsg: this.inputVal,
								likeNum: 0,
								dislikeNum: 0,
								reviewNum: 0,
								reviewLess: []
							};
						uni.navigateTo({
							url: '../../pages/comment/comment?data='+ encodeURIComponent(JSON.stringify(data))
						})
					},1000);

	
				}

			
			},
			//表情是否开启
			biaoqingClick(e){
				this.isOpen = !this.isOpen
				console.log("??????")
				this.$emit('biaoqingClick', {
					isOpen: this.isOpen
				})
			},
			//表情选中添加表情
			emotion(e){
				this.isShow=false;
				this.inputVal+=e
			}
			
		}
	}
